import { deepPurple, indigo } from '@material-ui/core/colors';
import { createMuiTheme } from '@material-ui/core/styles';

const theme = createMuiTheme({
    palette: {
        primary: {
            main: deepPurple[400]
        },
        secondary: {
            main: indigo[400]
        }
    },
});

export default theme;